<template>
  <div>
    <el-card class="wsy-card">
      <div class="el-card_body">
        <el-alert title="注意：只允许为第三级分类设置相关参数！" type="warning" effect="dark" show-icon></el-alert>
        <div class="cascader-box">
          <span>选择商品分类:</span>
          <el-cascader
            v-model="selectedKeys"
            :options="categories"
            :props="cascaderProps"
            @change="handleChange"
          ></el-cascader>
          <el-tabs v-model="activeName" type="border-card" @tab-click="handleClick">
            <el-tab-pane label="动态参数" name="only">
              <div>
                <el-button type="primary" size="mini" @click="addAttr()">添加参数</el-button>
              </div>
              <el-table border :data="goriesTable" style="width: 100%">
                <el-table-column type="expand">
                  <template slot-scope="props">
                    <el-form label-position="left" inline class="demo-table-expand"></el-form>
                  </template>
                </el-table-column>
                <el-table-column label="#" type="index"></el-table-column>
                <el-table-column label="参数名称" prop="attr_name"></el-table-column>
                <el-table-column label="操作">
                  <template slot-scope="scope">
                    <el-button
                      size="small"
                      type="primary"
                      icon="el-icon-edit"
                      @click="editMotai(scope.row)"
                    >修改</el-button>
                    <el-button
                      size="small"
                      type="danger"
                      icon="el-icon-delete"
                      @click="removeMotai(scope.row)"
                    >删除</el-button>
                  </template>
                </el-table-column>
              </el-table>
            </el-tab-pane>
            <el-tab-pane label="静态属性" name="many">
                            <div>
                <el-button type="primary" size="mini" @click="addAttr()">添加属性</el-button>
              </div>
              <el-table border :data="goriesTable" style="width: 100%">
                <el-table-column type="expand">
                  <template slot-scope="props">
                    <el-form label-position="left" inline class="demo-table-expand"></el-form>
                  </template>
                </el-table-column>
                <el-table-column label="#" type="index"></el-table-column>
                <el-table-column label="属性名称" prop="attr_name"></el-table-column>
                <el-table-column label="操作">
                  <template slot-scope="scope">
                    <el-button
                      size="small"
                      type="primary"
                      icon="el-icon-edit"
                      @click="editMotai(scope.row)"
                    >修改</el-button>
                    <el-button
                      size="small"
                      type="danger"
                      icon="el-icon-delete"
                      @click="removeMotai(scope.row)"
                    >删除</el-button>
                  </template>
                </el-table-column>
              </el-table>
            </el-tab-pane>
          </el-tabs>
        </div>
      </div>
    </el-card>

    <!-- 修改参数模态框 -->
    <el-dialog :title="str" :visible.sync="editMotal">
      <el-form
        :model="editformData"
        :rules="editrules"
        ref="editForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item :label='str1' prop="attr_name">
          <el-input v-model="editformData.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editMotal = false">取 消</el-button>
        <el-button type="primary" @click="Editsubmit()">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 添加参数模态框 -->
    <el-dialog :title='str' :visible.sync="addMotal">
      <el-form
        :model="addformData"
        :rules="editrules"
        ref="editForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item :label="str1" prop="attr_name">
          <el-input v-model="addformData.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addMotal = false">取 消</el-button>
        <el-button type="primary" @click="Addsubmit()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      activeName: "only",
      goriesTable: [],
      categories: [],
      selectedKeys: [],
      cascaderProps: {
        value: "cat_id",
        label: "cat_name",
        expandTrigger: "hover"
      },
      // 标签页name
      sel: [],
      // 修改动态参数模态框状态
      editMotal: false,
      // 添加动态参数模态框
      addMotal: false,
      editformData: {
        attr_id: 1623,
        attr_name: "",
        attr_sel: "",
        attr_vals: "",
        attr_write: "",
        cat_id: 0,
        delete_time: null
      },
      // 添加模态框表单数据
      addformData: {
        attr_name: "",
        attr_sel: "",
        cat_id: 0
      },
      // 修改参数数据模态框表单验证
      editrules: {
        attr_name: [
          { required: true, message: "参数名称不能为空", trigger: "blur" }
        ]
      },
      str:"设置动态参数",
      str1:"动态参数"
    };
  },
  created() {},
  mounted() {
    this.getCategories();
  },
  activated() {},
  update() {},
  beforeRouteUpdate(to, from, next) {
    next();
  },
  methods: {
    handleClick(tab, event) {
      this.sel[0] = tab.name;
      this.handleChange();
    },
    // 获取商品分类数据
    async getCategories() {
      const { data } = await this.$axios.get("categories", {
        params: { type: 3 }
      });

      this.categories = data.data;
    },
    handleChange() {
      let id = this.selectedKeys[this.selectedKeys.length - 1];

      let sel = this.sel == "" ? this.activeName : this.sel;
      this.getCategoriesTable(id, sel);
    },
    async getCategoriesTable(id, sel) {
      const { data } = await this.$axios.get(`categories/${id}/attributes`, {
        params: {
          sel
        }
      });
      this.goriesTable = data.data;
    },
    // 修改参数名称模态框
    editMotai(info) {
      // console.log(info);
      this.editformData = JSON.parse(JSON.stringify(info));
      this.editMotal = true;
    },
    // 点击确定修改参数名称
    async Editsubmit() {
      let sel = this.sel == "" ? this.activeName : this.sel;
      const { data } = await this.$axios.put(
        `categories/${this.editformData.cat_id}/attributes/${this.editformData.attr_id}`,
        {
          attr_name: this.editformData.attr_name,
          attr_sel: sel
        }
      );
      if (data.meta.status == 200) {
        this.editMotal = false;
        this.$message({
          message: data.meta.msg,
          type: "success"
        });
      } else {
        this.$message({
          message: data.meta.msg,
          type: "error"
        });
      }
    },
    // 点击显示提交模态框
    addAttr() {
      this.addMotal = true;
    },
    // 添加动态参数
    async Addsubmit() {
      let sel = this.sel == "" ? this.activeName : this.sel;
      const { data } = await this.$axios.post(
        `categories/${this.addformData.cat_id}/attributes`,
        {
          attr_name: this.addformData.attr_name,
          attr_sel: sel
        }
      );

      if (data.meta.status == 201) {
        let id = this.selectedKeys[this.selectedKeys.length - 1];

        let sel = this.sel == "" ? this.activeName : this.sel;
        this.getCategoriesTable(id, sel);
        this.addMotal = false;
        this.$message({
          message: data.meta.msg,
          type: "success"
        });
      } else {
        this.$message({
          message: data.meta.msg,
          type: "error"
        });
      }
    },
    removeMotai(info) {
      console.log(info);
      this.$confirm("此操作将永久删除该参数, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$axios
            .delete(`categories/${info.cat_id}/attributes/${info.attr_id}`)
            .then(({ data }) => {
              if (data.meta.status == 200) {
                let id = this.selectedKeys[this.selectedKeys.length - 1];

                let sel = this.sel == "" ? this.activeName : this.sel;
                this.getCategoriesTable(id, sel);

                this.$message({
                  type: "success",
                  message: data.meta.msg
                });
              } else {
                this.$message({
                  type: "error",
                  message: data.meta.msg
                });
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    }
    // removeFun()
  },
  filters: {},
  computed: {},
  watch: {
    activeName(val){
      if(val == "only"){
        this.str = '添加动态参数'
        this.str1 = '动态参数'
      }else{
        this.str = '添加静态属性'
        this.str1='静态属性'
      }
    }
  }
};
</script>

<style lang='scss' scoped>
.wsy-card {
  margin-top: 15px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15) !important;
  border: 1px solid #ebeef5;
  background-color: #fff;
  color: #303133;
  transition: 0.3s;
  border-radius: 4px;
  overflow: hidden;
  > .el-card_body {
    padding: 20px;
  }
}
.cascader-box {
  width: 100%;
  margin: 15px 0;
  > span {
    margin-right: 10px;
  }
}
.el-tabs {
  margin-top: 15px;
}
</style>
